.sidebar{
    flex: 0.35;
    display: flex;
    flex-direction: column;
}
.sidebar_header{
    
    display:flex;
    justify-content: space-between;
    padding:20px;
    border-right: 1px solid lightgray ;  
}
.sidebar_headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar_headerRight > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}
.sidebar_search{
    display:flex;
    align-items: center;
    background-color: white;
    padding:10px;
    height:10px;
    margin-bottom: 20px;
}
.sidebar_searchcontainer{
    display:flex;
    align-items: center;
    background-color: white;
    width:100%;
    height:15px;
    border-radius: 20px;
}
.sidebar_searchcontainer > input{
   border:none;
   padding:10px;
   height:10px;
   width:100%;
}
.sidebar_searchcontainer > .MuiSvgIcon-root{
    padding:10px;
    color:gray;
}
.sidebar_chat{
    flex:1;
    background-color: white;
    overflow:scroll;
    
}
