.chat{
    flex:0.65;
    display:flex;
    flex-direction: column;
}
.chat_header{
    display:flex;
    padding:18px;
    border-bottom: 1px solid lightgray;
    
}
.chat_headerinfo{
    flex:1;
   
    padding-left:20px;
}
.chat_headerRight{
    justify-content: right;
    min-width: 100px;
}
.chat_headerinfo > p{
    margin-top: 5px;
    color:gray;
}
.chat_body{
    flex:1;
    background-image: url("https://fsa.zobj.net/crop.php?r=HPh6XjS0Se_VNODSUR6Fm1R9bTHZZaIQV6BsKvbihNqsgOSM3Ftm58HrQ7nlrF9R65nUhHXkiA34Cu7Y-5-p1HjQHAcMj7SGoQS1XmK8i_Su-brIg-80w-9HV-s5c4w8z4JjR8VarCwJf8G_O4NlM5gHNQF7uHvtbJqCEL3X3fXhBsF3EdkZTrdef64jzBx9PwjBSJ23C_QtWaUi");
    background-repeat:repeat;
    background-position: center;
    padding:30px;
    overflow:scroll;
}
.chat_message{
    position:relative;
    border:1px solid black;
    font-size: 16px;
    padding:5px;
    border-radius: 20px;;
    width:fit-content;
    background-color: #ffffff;
    margin-bottom:15px;
}
.chat_reciever{
    margin-left: auto;
    background-color: #dcf8c6;

}
.chat_name{
    position:absolute;
    top: -13px;
    font-size: xx-small;
    font-weight: 800;

}
.chat_timestamp{
    margin-left: 10px;
    font-weight: bold;
    font-size: xx-small;

}
.chat_footer{
    display: flex;
    align-items: center;
    padding:20px;
    justify-content: space-between;
    border-top: 1px solid lightgray;
    

    
}
.chat_footer > form{
    flex:1;
    display:flex;
    
    width:100%;
    
    
}
.chat_footer > form > input{
    flex:1;
    border:hidden;
    border-radius: 20px;
    padding:10px;
    margin-left: 10px;
    margin-right: 10px;
}
.chat_footer > form > button{
    display:none;

   
}
